import React from 'react';
import ParallaxSlide from './ParallaxSlide.js';

export default class Main extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            activeParallax: 0
        }
    }

    onClickHandler = (event, parallaxSlideId) => {
        this.setState({activeParallax: parallaxSlideId});
    }

    render() {
        return(
            <div className="bigContainer">
                <button type="button" onClick={(event) => this.onClickHandler(event, 0)}>Who is Hera?</button>
                <button type="button" onClick={(event) => this.onClickHandler(event, 1)}>Command overview</button>
                <button type="button" onClick={(event) => this.onClickHandler(event, 2)}>Credits</button>
                <ParallaxSlide activeParallax={this.state.activeParallax}/>
            </div>
        );
    }
}



